import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import firebase from '../../fireconnection';


class Categorias extends Component {
    constructor(props) {
        super(props);
        this.state = {
            todasCategorias:[]
        };
        
       
    }
    componentDidMount(){
        let state= this.state;
        let categoriasFiltradas =[]
       firebase.database().ref('Filmes').on('value',(snapshot)=>{
           snapshot.forEach((filme)=>{
               if(filme.val().categoria === 'acao'){
                state.todasCategorias.push(
                    'ação'
                )
               }else{
                state.todasCategorias.push(
                    filme.val().categoria
                )
               }
               categoriasFiltradas = state.todasCategorias.filter((item,i)=> state.todasCategorias.indexOf(item) === i);
               
               
           })
           this.state.todasCategorias = categoriasFiltradas;
               this.setState(state);
               
       })
     
       
       
    }
    render() {
        return (
            <div>
               
                            <div className=' tags' id='botoes-categorias' >
                                {
                                    this.state.todasCategorias.map((categoria)=>{
                                        return(
                                            <Link to={`/filmes/categorias/${categoria}`}> <button type="button" class="text-uppercase botao">{categoria}</button></Link>
                                        )
                                    })
                                }
                        </div>
                       
               
            </div>
        )
    }
}
export default Categorias;