import React from 'react';
import {BrowserRouter,Switch,Route,Redirect} from 'react-router-dom';
import Header from './Components/Header'
import Home from './Pages/Home';
import Sobre from './Pages/Sobre';
import Painel from './Pages/Painel';
import Filmes  from './Pages/Filmes';
import Filme from './Pages/Filme';
import PainelCadastroFilmes from './Pages/Painel/painelDeCadastroFilmes';
import FilmeCategoria from './Pages/FilmeCategoria';
import FilmesBusca from './Pages/FilmesBusca';
import PainelCadastroAdmin from './Pages/Painel/painelDeCadastroAdmin';
import PainelMenu from './Pages/Painel/painelMenu';
import PainelRemoverFilmes from './Pages/Painel/painelRemoverFilmes';
import PainelEditarFilmes from './Pages/Painel/painelEditarFilmes';
import Footer from './Components/Footer'
const routes = () =>{
    
    return(
    <BrowserRouter>
        <Header/>
        <Switch>
            <Route exact path='/' component={Home}/>
            <Route exact path='/filme/:id/:titulo' component={Filme}/>
            <Route exact path='/filmes' component={Filmes}/>
            <Route exact path='/filmes/categorias/:categoria' component={FilmeCategoria}/>
            <Route exact path='/filmes/busca/:campoBusca' component={FilmesBusca}/>
            <Route path='/sobre' component={Sobre}/>
            <Route exact path='/painel' component={Painel}/>
            <Route exact path='/painel/menu' component={PainelMenu}/>
            <Route exact path='/painel/cadastro/filmes' component={PainelCadastroFilmes}/>
            <Route exact path='/painel/editar/filmes' component={PainelEditarFilmes}/>
            <Route exact path='/painel/remover/filmes' component={PainelRemoverFilmes}/>
            <Route exact path='/painel/cadastro/admin' component={PainelCadastroAdmin}/>
        </Switch>
        <Footer/>
    </BrowserRouter>
    )
}
export default routes;