 import React,{Component} from 'react';
 import firebase from '../../fireconnection';
 import validator from 'validator';

 class SelectCategorias extends Component{
     constructor(props){
         super(props);
         this.state ={
            todasCategorias:[],
            categoria:''
         }
         
        
        
     }
     componentDidMount(){
         let state= this.state;
         let categoriasFiltradas =[]
        firebase.database().ref('Filmes').on('value',(snapshot)=>{
            snapshot.forEach((filme)=>{
                state.todasCategorias.push(
                    filme.val().categoria
                )
                categoriasFiltradas = state.todasCategorias.filter((item,i)=> state.todasCategorias.indexOf(item) === i);
                
                
            })
            this.state.todasCategorias = categoriasFiltradas;
                this.setState(state);
                
        })
      
        
     
        
        
     }
     
     
     render(){
         return(
             <div className='selectcategorias'>
                 <legend>Escolha sua Categoria:</legend>
                 <select class="form-control"onChange={(e)=>this.props.value(e.target.value)}>
                 <option selected>Escolher</option>
                 {
                     this.state.todasCategorias.map((categoria)=>{
                         
                         return(
                             
                            <option>{categoria}</option>
                         )
                        
                     })
                 }
                 </select>
                 

             </div>
         )
     }
 }
 export default SelectCategorias;