import React,{Component} from 'react';
import firebase from '../../fireconnection';
import {Link} from 'react-router-dom';
import {AlertaSucess,AlertaDanger} from '../../Utils/AlertaStyled';
import Alerta from '../../Utils/Alertas';
class Painel extends Component{
    constructor(props){
        super(props);
        this.stateInicial ={
            email:'',
            senha:''
        }
        this.state = this.stateInicial;
        this.entrar = this.entrar.bind(this);
        this.sair = this.sair.bind(this);
        this.cadastrar = this.cadastrar.bind(this);
        this.limpaFormulario = this.limpaFormulario.bind(this);
        firebase.auth().onAuthStateChanged((user)=>{
            if(user){
            
              this.props.history.push('/painel/menu');
            }
          })
      
        
    }
    limpaFormulario(){
        this.setState(this.stateInicial)
    }                                                                                                                                    
    entrar(e){
        e.preventDefault();                                                                                                                                                                                                     
        firebase.auth().signInWithEmailAndPassword(this.state.email,this.state.senha)
        .then((r)=>{
            return(
                <Link to='/painelogin'>teste</Link>
            )
            
        })
        .catch((error)=>{
            if(error.code === 'auth/user-not-found'){
                Alerta.ShowAlertDanger('alertaerro')
                this.limpaFormulario();
            }
            if(error.code === 'auth/invalid-email'){
                Alerta.ShowAlertDanger('alertaerroemail');
                this.limpaFormulario();
            }
            if(error.code === 'auth/wrong-password'){
                Alerta.ShowAlertDanger('alertaerrosenha');
                this.limpaFormulario();
            }else{
                console.log('Error: '+ error.code);
                
            }
        })
    }
    sair(){
        firebase.auth().signOut();
        alert('Deslogado com sucesso')
    }
    cadastrar(){
        this.props.history.push('/painel/cadastro/admin')
    }
    render(){
        return(
            <div className='container painel-login'>
                <h1>Painel de Cadastro</h1>
                <AlertaDanger id='alertaerro'>Administrador não encontrado!</AlertaDanger>
                <AlertaDanger id='alertaerroemail'>Email invalido!</AlertaDanger>
                <AlertaDanger id='alertaerrosenha'>Senha incorreta!</AlertaDanger>
                <form class="form-group" onSubmit={this.entrar}>
                  <label for="email">Login:</label>
                  <input type="email"
                    class="form-control" id="email" aria-describedby="helpId" required placeholder="Digite seu email:" value={this.state.email} onChange={(e)=>this.setState({email:e.target.value})}/>
                    <label for="senha">Senha:</label>
                    <input type="password"
                    class="form-control" name="" id="senha" aria-describedby="helpId" required placeholder="Digite sua senha:" value={this.state.senha} onChange={(e)=>this.setState({senha:e.target.value})}/>
                    <div className='botoes-login'>
                    <button type='submit' name="entrar" id="entrar" class="botao botao-painel-login" href="#" role="button" >Entrar</button>
                    <a name="entrar" id="cadastrar-admin" class="botao botao-painel-login" href="#" role="button" onClick={this.cadastrar}>Cadastrar</a>
                    </div>
                </form>
            </div>
        )
    }
} 
export default Painel;