 import React,{Component} from 'react';
import firebase from '../../fireconnection';
import {Link} from 'react-router-dom';
import validator from 'validator';
import { SectionMovies, TituloFilmes } from '../Filmes/styles';
 class FilmeCategoria extends Component{
     constructor(props){
         super(props);
         this.state ={
            filmes:[]
         }
         this.showFilmes = this.showFilmes.bind(this);
     }
     componentDidMount(){
        let state = this.state;
        let Filmes=firebase.database().ref('Filmes');
        Filmes.on('value',(snapshot)=>{
            snapshot.forEach((item)=>{
                state.filmes.push({
                    titulo:item.val().titulo,
                    sinopse:item.val().sinopse,
                    capa:item.val().capa,
                    video:item.val().video,
                    id:item.val().id,
                    categoria:item.val().categoria
            })
            })
  
            this.setState(state);
            
        })
     }
     showFilmes(){
         this.props.history.push('/filmes')
     }
     render(){
         return(
            <div className='container d-flex flex-column justify-content-center align-items-center'>
                <TituloFilmes className='titulo-filme-categoria text-uppercase'>{this.props.match.params.categoria}</TituloFilmes>
                <button className='botao botao-categoria' onClick={this.showFilmes}>Voltar para Todos</button>
                <SectionMovies className='categoria-filmes'>
                    
                {
                    this.state.filmes.map((filme,index)=>{
                        if(this.props.match.params.categoria === 'ação'){
                            this.props.match.params.categoria = 'acao';
                        }
                        if(validator.equals(this.props.match.params.categoria,filme.categoria)){
                            return(
                                <div className='filmes-categoria' key={index}>
                                    <Link to={`/filme/${filme.id}/${filme.titulo}`}className='d-flex flex-wrap'> <span>{filme.titulo}</span>
                                    <img className='img-fuild'src={filme.capa}/>
                                    </Link>
                                </div>
                            )
                        }
                        
                    })
                }
                </SectionMovies>
            </div>
         )
     }
 }
 export default FilmeCategoria;