import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';


class InputBusca extends Component {
    constructor(props){
        super(props);
        this.stateInicial={
            campoBusca:'',
            submit:false
           
        }
        this.state = this.stateInicial
        this.buscar = this.buscar.bind(this)
          
    }
    buscar(e){
        console.log('Foi o submit');
        
        e.preventDefault();
        let state = this.state;
        state.submit = true;
        
        
    }
    isActive(){

    }
    render() {
        return (
            <div>
                <form class="input-group mb-3 buscar-form" onClick={this.buscar}>
                <input value={this.state.campoBusca} required type="text" class="buscar form-control" placeholder="Buscar um filme" aria-label="Usuário" aria-describedby="basic-addon1"
                onChange={(e)=> this.setState({campoBusca:e.target.value})}/>
                    <div class="input-group-append">
                    <span  class="input-buscar input-group-text" id="basic-addon1" ><Link to ={`/filmes/busca/${this.state.campoBusca}`}><button type='submit' role="button"
                          class="fas fa-search" ></button></Link></span>
                    </div>
                </form>
                </div>
                )
            }
        }
export default InputBusca;