import React, { Component } from 'react';
import firebase from '../../fireconnection';
import $ from 'jquery';
import Alerta from '../../Utils/Alertas';
import { AlertaSucess, AlertaDanger } from '../../Utils/AlertaStyled';
import './painel.css';
import validator from 'validator';
import SelectCategorias from '../../Components/Categorias/SelectCategorias';
export default class PainelEditarFIlmes extends Component {
    constructor(props) {
        super(props)
        this.stateInicial = {
            campoBusca: '',
            campo: '',
            campoEditado: '',
            titulo: '',
            sinopse: '',
            video: '',
            capa: '',
            categoria: '',
            id: ''
        }
        this.state = this.stateInicial;
        this.state = { check: false }
        this.buscar = this.buscar.bind(this);
        this.editar = this.editar.bind(this);
        this.showCampoEditar = this.showCampoEditar.bind(this);
        this.voltarMenu = this.voltarMenu.bind(this);
        this.getCategoria = this.getCategoria.bind(this);


    }
    componentDidMount() {
        firebase.auth().onAuthStateChanged((user) => {
            if (user) {
                this.setState({ check: true })

            }
        })
        $('#campoeditarcategoria').hide();

    }
    buscar() {
        let check = false;
        firebase.database().ref('Filmes').once('value', (snapshot) => {
            snapshot.forEach((filme) => {
                if ((this.state.campoBusca === filme.val().id) || (this.state.campoBusca === filme.val().titulo)) {
                    
                    $('#encontrou').slideDown();
                    this.setState({
                        titulo: filme.val().titulo,
                        sinopse: filme.val().sinopse,
                        categoria: filme.val().categoria,
                        capa: filme.val().capa,
                        video: filme.val().video,
                        id: filme.val().id
                    })
                    check = true;
                    Alerta.ShowAlertSucess('alertafilmencontrado');
                    this.setState({ campoBusca: '' });
                }
            })

            if (!check) {
                Alerta.ShowAlertDanger('alertafalha');
                this.setState({ campoBusca: '' });
            }

        })
    }
    esconderCampo() {
        $('#encontrou').hide()
        $('#campoeditar').hide()
        $('#campoeditarcategoria').hide();
    }
    showCampoEditar(campo){
        this.setState({campo:campo});
        if(campo === 'categoria'){
            $('#encontrou').slideUp();
            $('#campoeditarcategoria').slideDown();
        }else{
            $('#encontrou').slideUp();
            $('#campoeditar').slideDown();
        }
    }
    sair() {
        $('#campoeditar').hide();
        $('#campoeditarcategoria').hide();
    }
    editar() {
        
        if (this.state.campoEditado !== '') {
            
            switch (this.state.campo) {
                case 'titulo':
                    console.log('oi')
                    firebase.database().ref('Filmes').child(`${this.state.id}`).set({
                        titulo: this.state.campoEditado,
                        sinopse: this.state.sinopse,
                        categoria: this.state.categoria,
                        capa: this.state.capa,
                        video: this.state.video,
                        id: this.state.id
                    })
                    Alerta.ShowAlertSucess('alertasucess');
                    this.setState({ campoEditado: '' })
                    break;
                case 'categoria':
                    firebase.database().ref('Filmes').child(`${this.state.id}`).set({
                        titulo: this.state.titulo,
                        sinopse: this.state.sinopse,
                        categoria: this.state.campoEditado,
                        capa: this.state.capa,
                        video: this.state.video,
                        id: this.state.id
                    })
                    Alerta.ShowAlertSucess('alertasucess');
                    this.setState({ campoEditado: '' })
                    break;
                case 'sinopse':
                    firebase.database().ref('Filmes').child(`${this.state.id}`).set({
                        titulo: this.state.titulo,
                        sinopse: this.state.campoEditado,
                        categoria: this.state.categoria,
                        capa: this.state.capa,
                        video: this.state.video,
                        id: this.state.id
                    })
                    Alerta.ShowAlertSucess('alertasucess');
                    this.setState({ campoEditado: '' })
                    break;
                case 'capa':
                    if (validator.isURL(this.state.campoEditado)) {
                        firebase.database().ref('Filmes').child(`${this.state.id}`).set({
                            titulo: this.state.titulo,
                            sinopse: this.state.sinopse,
                            categoria: this.state.categoria,
                            capa: this.state.campoEditado,
                            video: this.state.video,
                            id: this.state.id
                        })
                        Alerta.ShowAlertSucess('alertasucess');
                        this.setState({ campoEditado: '' })
                    } else {
                        Alerta.ShowAlertDanger('alertafalhaurl');
                        this.setState({ campoEditado: '' })
                    }
                    break;
                case 'video':
                       
                        
                    if (validator.isURL(this.state.campoEditado)) {
                        if(validator.contains(this.state.campoEditado,'youtube')){
                            let codigoSeparados = this.state.campoEditado.split('=');
                            let codigoVideo = codigoSeparados.pop();
                            this.state.campoEditado = `https://www.youtube.com/embed/${codigoVideo}`;
                            this.setState({campoEditado:this.state.campoEditado})
    
    
                            firebase.database().ref('Filmes').child(`${this.state.id}`).set({
                                titulo: this.state.titulo,
                                sinopse: this.state.sinopse,
                                categoria: this.state.categoria,
                                capa: this.state.capa,
                                video: this.state.campoEditado,
                                id: this.state.id
                            })
                            Alerta.ShowAlertSucess('alertasucess');
                            this.setState({ campoEditado: '' })
                        }else{
                            Alerta.ShowAlertDanger('falhayoutube');
                            this.setState({ campoEditado: '' })
                        }
                    } else {
                        Alerta.ShowAlertDanger('alertafalhaurl');
                        this.setState({ campoEditado: '' })
                    }
                    break;
            }

            $('#campoeditar').hide();
        } else {
            Alerta.ShowAlertDanger('alertacampovazio')
        }


    }
    voltarMenu() {
        this.props.history.push('/painel/menu')
    }
    getCategoria(valor) {
        let state = this.state;
        state.campoEditado = valor;

        this.setState(state)
        

    }

    render() {
        return (
            <div>
                {
                    this.state.check &&

                    (
                        <div className='container painel-editar'>
                            <h1>Painel Editar</h1>
                            <AlertaSucess id='alertafilmencontrado'>O filme {this.state.titulo} foi encontrado com sucesso!</AlertaSucess>
                            <AlertaSucess id='alertasucess'>{this.state.campo} editado com sucesso do filme {this.state.titulo}!</AlertaSucess>
                            <AlertaDanger id='alertafalha'>ID ou Tilulo não encontrado!</AlertaDanger>
                            <AlertaDanger id='alertacampovazio'>O campo está vazio!</AlertaDanger>
                            <AlertaDanger id='alertafalhaurl'>A Capa ou o Video precisam ser no formato Link(URL)!</AlertaDanger>
                            <AlertaDanger id='falhayoutube'>O link do video precisa ser do youtube!</AlertaDanger>
                            <div class="form-group painel-busca">
                                <label for="id">Digite o ID ou Titulo do Filme que deseja editar:</label>
                                <input type="text"
                                    class="form-control" name="id" id="id" aria-describedby="helpId" placeholder="Digite o ID ou o titulo do filme"
                                    value={this.state.campoBusca} onChange={(e) => this.setState({ campoBusca: e.target.value })} />
                                    <div className='botoes-editar'>
                                <a name="buscar" id="buscar" class="botao btn-painel-editar " href="#" role="button" onClick={this.buscar}>Buscar</a>
                                <a name="voltar" id="voltar" class="botao btn-painel-editar" href="#" role="button" onClick={this.voltarMenu}>Voltar</a>
                                </div>
                            </div>
                            <div id='encontrou' className='painel-editar-escolha'>
                                <h2>Escolha o que deseja editar abaixo:</h2>
                                <a name="titulo" id="titulo" class="botao" href="#" role="button" onClick={()=>this.showCampoEditar('titulo')}>Titulo</a>
                                <a name="categoria" id="categoria" class="botao" href="#" role="button" onClick={()=>this.showCampoEditar('categoria')}>Categoria</a>
                                <a name="sinopse" id="sinopse" class="botao" href="#" role="button" onClick={()=>this.showCampoEditar('sinopse')}>Sinopse</a>
                                <a name="capa" id="capa" class="botao" href="#" role="button" onClick={()=>this.showCampoEditar('capa')}>Capa</a>
                                <a name="video" id="video" class="botao" href="#" role="button" onClick={()=>this.showCampoEditar('video')}>Video</a>
                            </div>
                            <div id='campoeditar'>
                                <div className='campoeditar'>
                                    <label>Digite o(a) {this.state.campo}:</label>
                                    <input class="form-control" type='text' onChange={(e) => this.setState({ campoEditado: e.target.value })} value={this.state.campoEditado} />
                                    <div className='botoes-editar'>
                                    <a name="editar" id="editar" class="botao btn-painel-editar" href="#" role="button" onClick={this.editar}>Editar</a>
                                    <a name="sair" id="sair" class="botao btn-painel-editar" href="#" role="button" onClick={this.sair}>Sair</a>
                                    </div>
                                </div>
                            </div>
                            <div id='campoeditarcategoria'>
                                <SelectCategorias value={this.getCategoria} />
                                <div className='d-flex '>
                                    <a name="editar" id="editar" class="botao btn-painel-editar" href="#" role="button" onClick={this.editar}>Editar</a>
                                    <a name="sair" id="sair" class="botao btn-painel-editar" href="#" role="button" onClick={this.sair}>Sair</a>
                                </div>
                            </div>
                        </div>
                    )
                }


            </div>
        )
    }
}