 import React,{Component} from 'react';
 import firebase from '../../fireconnection';
 import $ from 'jquery';
 import {Link }from 'react-router-dom';

 class HeaderFilmesDropdown extends Component{
     constructor(props){
         super(props);
         this.state={
             todasCategorias:[]
         }
         
     }
     componentDidMount(){
        let state= this.state;
        let categoriasFiltradas =[]
       firebase.database().ref('Filmes').on('value',(snapshot)=>{
           snapshot.forEach((filme)=>{
               if(filme.val().categoria === 'acao'){
                state.todasCategorias.push(
                    'ação'
                )
               }else{
                state.todasCategorias.push(
                    filme.val().categoria
                )
               }
               categoriasFiltradas = state.todasCategorias.filter((item,i)=> state.todasCategorias.indexOf(item) === i);
               
               
           })
           this.state.todasCategorias = categoriasFiltradas;
               this.setState(state);
               
       })
     }
     render(){
         return(
             <li className='nav-item dropdown' id='dropdown-filmes'>
                 <a class="nav-link dropdown-toggle" id='filmes-dropdown' data-toggle="dropdown" href="#" role="button" aria-haspopup="true" aria-expanded="false"
                 onClick={()=>$('.dropdown-menu').slideToggle()}>FILMES</a>
                 <div className='dropdown-menu'>
                <Link className='dropdown-item text-uppercase' to='/filmes'  onClick={()=>$('.dropdown-menu').toggle()}><span>Todos</span></Link>
                    {
                        this.state.todasCategorias.map((categoria)=>{
                            return(
                                <Link to={`/filmes/categorias/${categoria}`} className='dropdown-item text-uppercase'
                                onClick={()=>$('.dropdown-menu').toggle()}><span>{categoria}</span></Link>
                            )
                            
                        })
                    }
                   
                 </div>
             </li>
         )
     }
 }
 export default HeaderFilmesDropdown;