import React, { Component } from 'react';
import firebase from '../../fireconnection';
import {AlertaSucess,AlertaDanger} from '../../Utils/AlertaStyled';
import Alerta from '../../Utils/Alertas';
import $ from 'jquery';
class PainelRemoverFilmes extends Component{
    constructor(props){
        super(props);
        this.state ={
            id:'',
            check:true,
            checkalerta:true
        }
        this.remover= this.remover.bind(this);
        this.voltarMenu = this.voltarMenu.bind(this);
        
            
        
    }
    componentDidMount(){
      /*  firebase.auth().onAuthStateChanged((user)=>{
            if(user){
               this.setState({check:true})
                
            }
        })*/
    }
    remover(){
        let check=false;
        firebase.database().ref('Filmes').on('value',(snapshot)=>{
            snapshot.forEach((filme)=>{
                if((this.state.id === filme.val().id)||(this.state.id === filme.val().titulo)){
                   Alerta.ShowAlertSucess('alertaremovido')
                    firebase.database().ref('Filmes').child(`${filme.val().id}`).remove();
                    this.setState({id:''})
                    check =true;
                }
            })
        })
        if(!check){
            Alerta.ShowAlertDanger('alertafalha');
            this.setState({id:''})
        }

       
    }
    voltarMenu(){
        this.props.history.push('/painel/menu')
    }
   
    render(){
        return(
            <div className='container'>
                {
                    this.state.check &&
                    (
                        <div className='painel-remover'>
                        <h1>Painel Remover</h1>
                    <div class="form-group painel-remover-form">
                        <AlertaSucess id='alertaremovido'>O Filme foi removido com sucesso!</AlertaSucess>
                        <AlertaDanger id='alertafalha'>ID ou titulo não encontrado!</AlertaDanger>
                      <label for="">Digite o ID do filme ou o Titulo que deseja remover:</label>
                      <input type="text"
                        class="form-control" name="" id="remover-input" aria-describedby="helpId" placeholder="Digite o ID ou o Titulo do filme"
                        value={this.state.id} onChange={(e)=>this.setState({id:e.target.value})}/>
                        <div className='botoes-remover'>
                        <a name="remover" id="remover" class="botao" href="#" role="button" onClick={this.remover}>Remover</a>
                        <a name="voltar" id="voltar" class="botao" href="#" role="button" onClick={this.voltarMenu}>Voltar</a>
                        </div>
                    </div>
                    </div>
                    )}
                    {
                        !this.state.check &&
                        (
                            <div>
                                <h1>Logue no Painel!</h1>
                            </div>
                        )
                    }
                
            </div>
        )
    }
}
export default PainelRemoverFilmes;