import React from 'react';

const Sobre = () =>{
    return(
        <div className='sobre'>
            <h1>Sobre</h1>
            <span>Este projeto foi desenvolvido e idealizado por mim no intuito de exercitar o meu aprendizado em HTML, CSS, JS, e principalmente
                o framework React JS que foi utilizado em todo processo de criação do site. Foi usado como banco de dados o sistema Firebase da Google.
                Toda a page está como Single Page Application(SPA) e design responsivo para aparelhos mobile. Estou estudando a area de desenvolvimento desdo do incio de 2019, principalmente area de Front-End desde Julho de 2019.<br/>
                O site possui a pagina Home onde está os ultimos 8 filmes adicionados(atualizando automaticamente), pagina com catalogo de todos os filmes e botões para selecionar cada categoria,
                pagina do filme onde tem o filme(hospedado no Youtube), sinopse, capa, uma area onde a pessoa pode comentar e ver os comentarios de todos os outros usuários e um campo de busca para pesquisar um filme.
                Além disso o site possui um sistema no painel onde o adiministrador pode:
                <ul>
                    <li> Cadastrar novos filmes. </li>
                    <li> Editar os filmes. </li>
                    <li> Remover algum filme. </li>
                    <li> Cadastrar novos adiministradores. </li>
                </ul>
                 
                
                 
                 
                Obrigado por ter acessado, se tiver alguma sugestão, dica ou critica me avise por favor em minhas redes sociais: <a href='https://www.linkedin.com/in/davidschnee/'><i class="fab fa-linkedin"></i></a>  
                <a href='https://github.com/DavidSchnee/DavidSchnee.github.io'><i class="fab fa-github"></i></a><a href='https://www.facebook.com/david.gelman1'><i class="fab fa-facebook"></i> </a> , estou sempre em busca de aprender. 

                
               <br/> <strong>OBS:</strong> Todos os campos de cadastro possui processo de validação, utilizei biblioteca do Validador JS em alguns casos.
               <br/> <strong>OBS:</strong>  Todos os filmes utilizados são trailers devidamente postados no Youtube com todos os direitos autorais reservados aos seus criadores.
                O intuito deste site foi meu treinamento, não a pirataria.  
            </span>
        </div>
    )
}
export default Sobre;