import React, { Component } from 'react';
import { HeaderStyled, LogoStyled, NavStyled } from './styles';
import {Link, NavLink} from 'react-router-dom';
import './index.css'
import $ from 'jquery';
import InputBusca from '../../Components/InputBusca';
import HeaderFilmesDropdown from './HeaderFilmesDropdown';




class Header extends Component{
    constructor(){
        super();
        this.showMenu= this.showMenu.bind(this);
        $('#navbarSupportedContent1').hide();
    }
    showMenu(){
        
        
        $('#navbarSupportedContent1').slideToggle();
    }
    render(){
        return(
            <div>
            <nav class="navbar navbar-light light-blue lighten-4">
              <button onClick={this.showMenu} class="navbar-toggler toggler-example" type="button" data-toggle="collapse" data-target="#navbarSupportedContent1"
                aria-controls="navbarSupportedContent1" aria-expanded="false" aria-label="Toggle navigation"><span class="dark-blue-text"><i
                    class="fas fa-bars fa-1x"></i></span></button>
            
            <LogoStyled>
                     <Link to='/'>
                         <span><i class="fas fa-file-video"></i> BLOCKMOVIES</span>
                     </Link>
                 </LogoStyled>
              
              <div class="menu collapse navbar-collapse" id="navbarSupportedContent1">
            
              <NavStyled className='items-menu-nav'>
                     <ul className=" items-menu nav justify-content-center">
                         <li className="nav-item" onClick={this.showMenu}>
                             <NavLink activeStyle={{color: "#130d0d"}} to='/sobre' className="nav-link" >SOBRE</NavLink>
                         </li>
                          <HeaderFilmesDropdown className='nav-item'/>  
                         <li className="nav-item" onClick={this.showMenu}>
                             <NavLink activeStyle={{color: "#130d0d"}}  className="nav-link" to='/painel'>PAINEL</NavLink >
                         </li>
                     </ul>
                 </NavStyled>
                 
                 <InputBusca/>
              </div>
              
            
            </nav>
             <HeaderStyled className='navdesktop'>
             <div className='container d-flex justify-content-between align-items-center'>
                 <LogoStyled>
                     <Link to='/'>
                     
                         <span><i class="fas fa-file-video"></i> BLOCKMOVIES</span>
                     </Link>
                 </LogoStyled>
                 <NavStyled>
                     <ul className="nav justify-content-center">
                         <li className="nav-item">
                             <NavLink activeStyle={{color: "#f8a480"}}  to='/sobre' className="nav-link" >SOBRE</NavLink>
                         </li>
                         <li className="nav-item">
                             <NavLink to='/filmes' activeStyle={{color: "#f8a480"}}  className="nav-link">FILMES</NavLink>
                         </li>
                         <li className="nav-item">
                             <NavLink activeStyle={{color: "#f8a480"}}  className="nav-link" to='/painel'>PAINEL</NavLink>
                         </li>
                         
                     </ul>
                 </NavStyled>
                 <InputBusca/>
                 
                </div>
            </HeaderStyled>
            </div>
        )
    }
} 
        
export default Header;