import $ from 'jquery';

export default class Alertas{
    constructor(){

    }

    static ShowAlertDanger(id){
        $(`#${id}`).show();
        setTimeout(()=>{
            $(`#${id}`).hide();
        },3000)
    }
    static ShowAlertSucess(id){
        $(`#${id}`).show();
        setTimeout(()=>{
            $(`#${id}`).hide();
        },4000)
    }
}