import React, { Component } from 'react';
import firebase from 'firebase';
import { AlertaDanger, AlertaSucess } from '../../Utils/AlertaStyled';
import Alertas from '../../Utils/Alertas';



class PainelCadastroAdmin extends Component {
    constructor(props) {
        super(props);
        this.stateInicial = {
            email: '',
            senha: '',
            key: '',
            token: ''
        }
        this.state = this.stateInicial;
        this.cadastrar = this.cadastrar.bind(this)
        this.limpaFormulario = this.limpaFormulario.bind(this);
        this.tokenReal = '147258369'
        this.tokenValidator = true;
        this.sair = this.sair.bind(this);
    }
    limpaFormulario() {
        this.setState(this.stateInicial)
    }
    sair(){
        console.log(this.props.history);
        
        this.props.history.push('/painel');
    }
    cadastrar(e) {
        e.preventDefault();
        if (this.state.token === this.tokenReal) {
            this.tokenValidator = false;
            firebase.auth().createUserWithEmailAndPassword(this.state.email, this.state.senha)
                .then((user) => {
                    Alertas.ShowAlertSucess('alertsucess');
                    this.setState({ key: user.user.uid })
                    firebase.database().ref('admins').child(`${this.state.key}`).set({
                        email: this.state.email,
                        senha: this.state.senha,
                        key: this.state.key
                    })
                    
                    this.limpaFormulario();
                })
                .catch((error) => {
                    console.log(error.code);
                    if(error.code === 'auth/invalid-email'){
                        Alertas.ShowAlertDanger('alertdangeremail');
                    }
                    this.limpaFormulario();
                })
        } else {
            this.tokenValidator = true;
            Alertas.ShowAlertDanger('alertdangertoken');
            this.limpaFormulario();
        }

    }
    render() {
        return (
            <div className='container painel-cadastro-admin'>
                <h1>Cadastro de Administradores</h1>
                <AlertaSucess id='alertsucess'>
                    Administrador cadastrado com sucesso!
                </AlertaSucess>
                <form class="form-group" onSubmit={this.cadastrar}>
                    <AlertaDanger id='alertdangeremail'>
                        Email Invalido!
                    </AlertaDanger>
                    <AlertaDanger id='alertdangertoken'>
                        Token Invalido!
                    </AlertaDanger>
                    <label for="email">Email:</label>
                    <input type="email" class="form-control" name="email" id="email" aria-describedby="helpId" placeholder="Digite seu email"
                        value={this.state.email} onChange={(e) => this.setState({ email: e.target.value })} required />
                    <label for="senha">Senha:</label>
                    <input type="password" class="form-control" name="senha" id="senha" aria-describedby="helpId" placeholder="Digite seu senha"
                        value={this.state.senha} onChange={(e) => this.setState({ senha: e.target.value })} required />
                    <label for="email">Token:</label>
                 
                    <input type="password" class="form-control" name="token" id="token" aria-describedby="helpId" placeholder="Digite seu token"
                        value={this.state.token} onChange={(e) => this.setState({ token: e.target.value })} required />
                    <div className='botoes-admin'>
                    <button type='submit' name="cadastar" id="cadastrar" class="botao botao-admin" href="#" role="button" >Cadastrar</button>
                    <a name="sair"  class="botao botao-admin" href="#" role="button" onClick={this.sair} >Sair</a>
                    </div>
                </form>
            </div>
        )
    }
}
export default PainelCadastroAdmin;