import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import firebase from '../../fireconnection';
import {PainelButton} from './PainelStyles';


class PainelMenu extends Component{
    constructor(props){
        super(props)
        this.sair = this.sair.bind(this);
    }
    sair() {
        firebase.auth().signOut();
        alert('Deslogado com sucesso!');
        this.props.history.push('/painel');
    }
    render(){
        return(
            <div className='container d-flex justify-content-center flex-column align-items-center painelmenu'>
                <h1>Painel Menu</h1>
                <br/>
                <Link name="cadastar" id="cadastar" class="botao botaomenu" to='/painel/cadastro/filmes' role="button">Cadastrar Filmes</Link>
                <br/>
                <Link name="editar"  class="botao botaomenu" to='/painel/editar/filmes' role="button">Editar Filmes</Link>
                <br/>
                <Link name="remover" id="remover" class="botao botaomenu" to='/painel/remover/filmes' role="button">Remover Filmes</Link>
                <br/>
                <a name="sair"  class="botao botaomenu" href="#" role="button" onClick={this.sair}>Sair do Painel</a>
            </div>
        )
    }
}
export default PainelMenu;