import React, { Component } from 'react';
import firebase from '../../fireconnection';
import { SectionMovies, TituloFilmes } from './styles';
import {Link} from 'react-router-dom';
import Categorias from '../../Components/Categorias';
import InputBusca from '../../Components/InputBusca';

class Filmes extends Component {

    constructor(props) {
        super(props);
        this.state = {
            listaFilmes: []
        }

     
        firebase.database().ref('Filmes').on('value', (snapshot) => {

            let state = this.state;
            state.listaFilmes = [];
            snapshot.forEach((filme) => {
                state.listaFilmes.push({
                    id: filme.val().id,
                    capa: filme.val().capa,
                    sinopse: filme.val().sinopse,
                    titulo: filme.val().titulo,
                    video: filme.val().video,
                    categoria:filme.val().categoria
                })

            })
            state.listaFilmes = state.listaFilmes.reverse();
            this.setState(state);
        })

    }


    render() {
        return (
            <div className='container d-flex flex-column justify-content-center align-items-center'>
                <TituloFilmes className='titulo-filmes'>CATALOGO DE FILMES</TituloFilmes>
                <Categorias className='botoes-categorias' value={this.state.listaFilmes}/>
                <SectionMovies className='d-flex flex-row align-items-center justify-content-around flex-wrap'>
                {
                    this.state.listaFilmes.map((filme,index) => {
                        return (
                            <div className='filme' key={index}>
                                <Link to={`/filme/${filme.id}/${filme.titulo}`}className='d-flex flex-wrap'> <span className='text-titulo'>{filme.titulo}</span>
                                <img className='img-fuild'src={filme.capa}/>
                                </Link>
                            </div>
                        )
                    })

                }
                </SectionMovies>
            </div>
        )
    }
}


export default Filmes;   