 import React,{Component} from 'react';
 import $ from 'jquery';
 import firebase from '../../fireconnection'
 import Alerta from '../../Utils/Alertas';
 import {AlertaDanger,AlertaSucess} from '../../Utils/AlertaStyled';



 class ComentarioBox extends Component{
     constructor(props){
         super(props);
         this.state={
            id:Date.now(),
            nome:'',
            email:'',
            comentario:'',
            filme:'',
            dataCompleta: [],
            ultimoComentario:[]
         }
        
         this.showBox = this.showBox.bind(this);
         this.comentar = this.comentar.bind(this);  
         console.log(props);
         
        
              
     }
    
     
     showBox(e){
         
         e.preventDefault();
         $('.form-group').slideToggle();
         $('#botao-comentar-principal').hide();
         
     }
     comentar(e){
        e.preventDefault();
        let data = new Date();
        let state = this.state
        state.dataCompleta  = [];
        state.dataCompleta.push({
            hora:data.getHours(),
            dia:data.getDate(),
            minutos: data.getMinutes(),
            mes:data.getMonth(),
            ano:data.getFullYear()
        })
        
        this.setState(state)
        
        firebase.database().ref('Comentarios').child(`${this.props.filme}`).child(`${this.state.id}`).set({
            nome:this.state.nome,
            email:this.state.email,
            comentario:this.state.comentario,
            id:Date.now(),
            filme:this.props.filme,
            hora:data.getHours(),
            dia:data.getDate(),
            minutos: data.getMinutes(),
            mes:data.getMonth(),
            ano:data.getFullYear()
        })
        
            firebase.database().ref('Comentarios').child(`${this.props.filme}`).limitToLast(1).once('value',(snapshot)=>{
                let state = this.state;
                snapshot.forEach((comentario) => {
                    state.ultimoComentario.push({
                        nome: comentario.val().nome,
                        email: comentario.val().email,
                        comentario: comentario.val().comentario,
                        hora: comentario.val().hora,
                        dia: comentario.val().dia,
                        minutos: comentario.val().minutos,
                        mes: comentario.val().mes,
                        ano: comentario.val().ano
                    })
                    this.setState(state);
                    console.log(state.ultimoComentario);
                    this.props.getUltimoComentario(state.ultimoComentario);
                    state.ultimoComentario = [];
                    this.setState(state);
            })
    
    
        })
        
        
        this.setState({
            id:Date.now(),
            nome:'',
            email:'',
            comentario:'',
        })
        $('.form-group').slideToggle(1000);
        $('#botao-comentar-principal').show();
        Alerta.ShowAlertSucess('alerta-sucess-comentario')
     }
     render(){
         return(
           
            <div className='comentar'>
                <AlertaSucess id='alerta-sucess-comentario'>O comentário foi adicionado com sucesso!</AlertaSucess>
                <a name="botao-comentar" id="botao-comentar-principal" class="botao" href="#" role="button"onClick={this.showBox}>Comentar</a>
                <form class="box-comentar form-group" onSubmit={this.comentar}>
                  <label for="email">Email:</label>
                  <input type="email" name="email" id="email" class="form-control" required placeholder="Digite seu email" aria-describedby="helpId" value={this.state.email} onChange={(e)=>this.setState({email:e.target.value})}/>
                  <label for="nome">Nome:</label>
                  <input type="text" name="nome" id="nome" class="form-control" required  placeholder="Digite seu nome" aria-describedby="helpId" value={this.state.nome} onChange={(e)=>this.setState({nome:e.target.value})}/>
                    <label for="comentario">Comentario:</label>
                    <textarea maxLength='50' class="form-control" name="comentario" id="comentario" required  rows="3" value={this.state.comentario} onChange={(e)=>this.setState({comentario:e.target.value})}></textarea>
                    <button type='submit' name="botao-comentar" id="botao-comentar" class="botao" href="#" role="button">Comentar</button>
                </form>
                
            </div>
            
         )
     }
 }
 export default ComentarioBox;