import React, { Component } from 'react';
import firebase from '../../fireconnection';
import { Link } from 'react-router-dom';
import $ from 'jquery';



class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            filmesLancamentos: []
        }
        this.showFilmes = this.showFilmes.bind(this);
    }
    componentDidMount() {
        firebase.database().ref('Filmes').limitToLast(8).on('value', (snapshot) => {
            let state = this.state;
            snapshot.forEach((filme) => {
                state.filmesLancamentos.push({
                    titulo: filme.val().titulo,
                    capa: filme.val().capa,
                    id: filme.val().id
                })
            })
            state.filmesLancamentos = state.filmesLancamentos.reverse();
            this.setState(state);
           
        })

    }
    showFilmes(){
        this.props.history.push('/filmes')
    }

    render() {
        return (
            <div className='container home'>
                <h1><i class="fas fa-file-video"></i> BLOCKMOVIES</h1>
                <h2>ULTIMOS LANÇAMENTOS!</h2>
                <section className='secao-lancamentos'>
                    {
                        this.state.filmesLancamentos.map((filme) => {
                            return (
                                <div className='lançametos'>
                                    <Link to={`/filme/${filme.id}/${filme.titulo}`}>
                                        <h3>{filme.titulo}</h3>
                                        <img src={filme.capa} />
                                    </Link>
                                </div>


                            )

                        })
                    }
                    <button className='botao botao-home' onClick={this.showFilmes}>VER TODOS</button>
                </section>

            </div>
        )
    }
}
export default Home