import styled from 'styled-components';

/* COR PADRÃO */
let corPadrao ='rgb(19, 13, 13);'
/* FONTE PADRÃO */
let fontePadrao ="'Josefin Sans', sans-serif"

export const PostMovie = styled.article `
font-family: ${fontePadrao};
display: flex;
justify-content: center;
align-items: center;
flex-direction: column;
    h3{
        margin-top:1em;
        color:${corPadrao};
        font-weight:bolder;
        font-size:2.5em;
    }
    img{
        width:290px;
        height:280px;
        margin:1em;
    }
    iframe{
        margin:1em;
    }
`