import React, { Component } from 'react';
import validador from 'validator';
import firebase from '../../fireconnection';
import { SectionMovies, TituloFilmes } from '../../Pages/Filmes/styles';
import {Link} from 'react-router-dom';
import InputBusca from '../../Components/InputBusca';

class FilmesBusca extends Component {
    constructor(props) {
        super(props);
        this.state = {
            filmesEncontrados: [],
            econtrou:  false,
            conteudoBusca:'',
            campoBusca:'',
            submit:false
        }
        this.escutadorSubmit = this.escutadorSubmit.bind(this);

    }
    componentDidMount() {
        console.log('foi montado');
        let state = this.state;
        state.campoBusca = this.props.match.params.campoBusca;
        state.campoBusca = state.campoBusca.replace(state.campoBusca.charAt(0),state.campoBusca.charAt(0).toUpperCase())
        console.log(state.campoBusca);
        this.setState(state);
        
        this.state.filmesEncontrados= []

        firebase.database().ref('Filmes').on('value', (snapshot) => {
            console.log('foi');
            
            snapshot.forEach((filme) => {

                if (validador.contains(filme.val().titulo, state.campoBusca)) {
                    this.state.filmesEncontrados.push({
                        id: filme.val().id,
                        capa: filme.val().capa,
                        sinopse: filme.val().sinopse,
                        titulo: filme.val().titulo,
                        video: filme.val().video,
                        categoria: filme.val().categoria
                    })
                    this.state.econtrou = true;
                }
            })
        })
        this.setState(state);

    }
    escutadorSubmit(submit,campoBusca){
        this.setState({submit:true})
        if(this.state.submit){
            this.props.history.push(`/filmes/busca/${campoBusca}`);
            console.log('foi');
            this.setState({submit:true})
        }
    }
    render() {
        return (
            
            <div className='container'>
                
                {
                    this.state.econtrou ?
                    <div>
                 <SectionMovies className='d-flex align-items-center justify-content-around flex-wrap filmes-buscar'>
                 <TituloFilmes className='titulo-filmes'>FILMES ECONTRADOS</TituloFilmes>
                     {
                         
                         this.state.filmesEncontrados.map((filme,index)=>{
                             return(
                                <div className='filme' key={index}>
                                    
                                <Link to={`/filme/${filme.id}/${filme.titulo}`}className='d-flex flex-wrap'> <span className='text-titulo'>{filme.titulo}</span>
                                <img className='img-fuild'src={filme.capa}/>
                                </Link>
                            </div>
                             )
                           
                         })
                     }
                     <button onClick={()=>this.props.history.push('/filmes')} className='botao-todos botao'>Voltar Para Todos</button>
                     </SectionMovies>
            </div>:
                    <SectionMovies className='d-flex align-items-center justify-content-around flex-wrap filmes-buscar'>
                    <TituloFilmes className='titulo-filmes'>FILME NÃO ENCONTRADO!</TituloFilmes>
                    <button onClick={()=>this.props.history.push('/filmes')} className='botao-todos botao'>Voltar Para Todos</button>
                    </SectionMovies>
                }
                
             </div>
        )
    }
}
export default FilmesBusca;